import { createStore } from 'vuex'

const setLocalCartList = (state) => {
  const { cartList } = state;
  const cartListString = JSON.stringify(cartList);
  localStorage.cartList = cartListString
}
const getLocalCartList = () => {
  if (localStorage.cartList) {
    return JSON.parse(localStorage.cartList)
  } else {
    return {}
  }
}
export default createStore({
  state: {
    cartList: getLocalCartList()
    // { shopId: {shopName:'', productList:{ productId: {_id: '1',} }}}
  },
  mutations: {
    changeItemQuantity(state, payload) {
      const { shopId, productId, item, num } = payload;
      const shopInfo = state.cartList[shopId] || {
        shopName: '', productList: {}
      }
      let product = shopInfo.productList[productId]
      if (!product) {
        item.count = 0
        product = item
      }
      product.count = product.count + num
      if (num > 0) { product.check = true }
      if (product.count < 0) { product.count = 0 }
      
      shopInfo.productList[productId] = product
      state.cartList[shopId] = shopInfo

      setLocalCartList(state)
    },
    changeShopName(state, payload) {
      const { shopId, shopName } = payload;
      const shopInfo = state.cartList[shopId] || {
        shopName: '', productList: {}
      }
      shopInfo.shopName = shopName;
      state.cartList[shopId] = shopInfo
      setLocalCartList(state)
    },
    setItemAllChecked(state, shopId) {
      const productList = state.cartList[shopId].productList
      if (productList) {
        for (const key in productList) {
          const product = productList[key]
          product.check = true
        }
      }
      setLocalCartList(state)
    },
    cleanCartProducts(state, shopId) {
      state.cartList[shopId].productList = {}
      setLocalCartList(state)
    },
    changeItemChecked(state, payload) {
      const { shopId, productId } = payload
      const productList = state.cartList[shopId].productList;
      const product = productList[productId]
      product.check = !product.check
      setLocalCartList(state)
    },
    clearOrderedItem(state, shopId) {
      const productList = state.cartList[shopId].productList;
      let notOrderedItemList = {};
      for (let i in productList) {
        const product = productList[i]
        if (!product.check && product.count > 0) {
          notOrderedItemList[i] = product
        }
      }
      state.cartList[shopId].productList = notOrderedItemList;
      setLocalCartList(state)
    }
  }
})
