import { createRouter, createWebHashHistory } from 'vue-router' 

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '../views/home/HomeView.vue')
  },
  {
    path: '/cart',
    name: 'cart',
    component: () => import(/* webpackChunkName: "cart" */ '../views/cart/CartView.vue')
  },
  {
    path: '/orderList',
    name: 'orderList',
    component: () => import(/* webpackChunkName: "orderList" */ '../views/orderList/OrderList.vue')
  },
  {
    path: '/my',
    name: 'my',
    component: () => import(/* webpackChunkName: "my" */ '../views/my/MyView.vue')
  },
  {
    path: '/adminAddress',
    name: 'adminAddress',
    component: () => import(/* webpackChunkName: "adminAddress" */ '../views/address/adminAddress.vue')
  },
  {
    path: '/createAddress',
    name: 'createAddress',
    component: () => import(/* webpackChunkName: "createAddress" */ '../views/address/createAddress.vue')
  },
  {
    path: '/editAddress',
    name: 'editAddress',
    component: () => import(/* webpackChunkName: "editAddress" */ '../views/address/editAddress.vue')
  },
  {
    path: '/order/:id',
    name: 'order',
    component: () => import(/* webpackChunkName: "order" */ '../views/order/orderConfirmation.vue')
  },
  {
    path: '/shop/:id',
    name: 'shop',
    component: () => import(/* webpackChunkName: "shop" */ '../views/shop/ShopView.vue')
  },
  {
    path: '/login',
    name: 'login',
    component: () => import(/* webpackChunkName: "login" */ '../views/login/LoginView.vue'),
    beforeEnter(to, from, next) {
      const { isLogin } = localStorage;
      isLogin ? next({ name: 'home' }) : next()
    }
  },
  {
    path: '/register',
    name: 'register',
    component: () => import(/* webpackChunkName: "register" */ '../views/register/RegisterView.vue'),
    beforeEnter(to, from, next) {
      const { isLogin } = localStorage;
      isLogin ? next({ name: 'home' }) : next()
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach((to, from, next) => {
  const { isLogin } = localStorage;
  (isLogin || to.name === 'login' || to.name === 'register') ? next() : next({ name: 'login' })
})
export default router
